import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { Routes, RouterModule } from '@angular/router';
import { SalesDashboardComponent } from './sales-dashboard.component';
import { LayoutModule } from '../../../layouts/layout.module';
import { DefaultComponent } from '../../default.component';
import {HttpClientModule} from '@angular/common/http';
import { HTTP_INTERCEPTORS } from '@angular/common/http';
import {BrowserAnimationsModule} from '@angular/platform-browser/animations';
import {CdkTableModule} from '@angular/cdk/table';
import {ListModule} from '../../list/list.module';
import {ListComponent, SafeHtmlPipe} from '../../list/list.component';
import {MatInputModule, MatNativeDateModule, MatCardModule, MatTableModule, MatSortModule, MatPaginatorModule } from '@angular/material';
	
const routes: Routes = [
    {
        "path": "",
        "component": DefaultComponent,
        "children": [
            {
                "path": "",
                "component": 
				SalesDashboardComponent
            }
        ]
    }
	
];
@NgModule({
    imports: [
        CommonModule, RouterModule.forChild(routes), LayoutModule, HttpClientModule,
        ListModule, MatInputModule, MatNativeDateModule, MatCardModule, MatTableModule, MatSortModule, MatPaginatorModule
    ], 
	exports: [ RouterModule ], 
	declarations: [ SalesDashboardComponent ],
})
export class SalesDashboardModule {

}


